.reviews__inner {
  padding: 0 10px 75px 12px;
  overflow: hidden;

  @media (max-width: $tablets-max) {
    padding: 0 112px 80px;
  }
}

.reviews__list {
  @include list;
  display: flex;
  flex-direction: row;
}

.reviews__inner h2 {
  font-weight: 800;
  font-size: 35px;
  line-height: 43px;
  margin: 0;
  padding-bottom: 36px;

  @media (max-width: $tablets-max) {
    font-size: 25px;
    line-height: 30px;
    padding-top: 54px;
  }
}

.reviews__item {
  width: 385.76px;
  background: #ffffff;
  border: 1px solid #ffffff;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
  padding: 45px 32px 32px 35px;
  flex-shrink: 0;

  @media (max-width: $tablets-max) {
    width: 388.76px;
  }

  &--first {
    padding: 45px 32px 32px 35px;
  }

  &--second {
    padding: 45px 8px 32px 32px;
  }

  &--third {
    padding: 42px 14px 32px 40px;
  }
  // margin-right: 15px;
}

.reviews__info {
  display: flex;
  padding-bottom: 42px;
}

.reviews__user-info {
  padding-left: 15px;
}

.reviews__user-info p {
  margin: 0;
  padding: 29px 0 5px;
  font-size: 14px;
  font-weight: bold;
  line-height: 19px;
}

.reviews__user-info span {
  font-weight: 400;
  font-size: 14px;
}

.reviews__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 23px;
}

.reviews__text p {
  margin: 0;
  padding-bottom: 20px;
}

.reviews__title-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  @media (max-width: $tablets-max) {
    padding-bottom: 15px;
  }
}

.reviews__buttons {
  display: flex;
  transform: translateY(-30px);
  margin-right: -6px;
}
