.offers-wrapper {
  overflow: hidden;
}

.offers {
  position: relative;
  // overflow: hidden;
  // background-position: 100% 41%;

  // &::before {
  //   position: absolute;
  //   content: "";
  //   background-image: url("../img/clouds@1x.png");
  //   background-repeat: no-repeat;
  //   width: 100%;
  //   left: 0;
  //   height: 979px;
  //   top: -680px;
  //   // z-index: 2;
  //   background-size: cover;
  // }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/unsplash-desktop@1x.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 559px;
    height: 961px;
    right: -37px;
    top: -20px;
    z-index: 1;
  }
}

.offers__inner {
  padding: 0 15px 0 10px;
  position: relative;
  z-index: 2;

  @media (max-width: $tablets-max) {
    padding: 0 112px;
  }
}

.offers__inner h2 {
  font-weight: 800;
  font-size: 35px;
  line-height: 43px;
  color: #2d383f;
  margin: 0;
  padding: 74px 0 0;

  @media (max-width: $tablets-max) {
    font-size: 25px;
    line-height: 30px;
    padding: 84px 0 0;
  }
}

.offers__swiper-container {
  color: #ffffff;
  padding: 50px 0 0;
  z-index: 2;

  @media (max-width: $tablets-max) {
    padding: 57px 0 0;
  }
}

.offers__list {
  @include list;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.offers__item {
  width: 384.17px;
  background-repeat: no-repeat;
  background-size: cover;
  flex-shrink: 0;

  &--elbrus {
    padding: 206px 56px 20px 22px;
    background-image: url("../img/elbrus-desktop@1x.jpg");
  }

  &--belukha {
    background-image: url("../img/belukha-desktop@1x.jpg");
    padding: 206px 38px 20px 22px;
  }

  &--blacksea {
    background-image: url("../img/blacksea-desktop@1x.jpg");
    padding: 206px 45px 20px 22px;
  }
}

.offers__period {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  padding-top: 8px;

  &--days {
    &::before {
      position: absolute;
      content: "";
      background-image: url("../img/sun.svg");
      width: 21.73px;
      height: 22px;
      top: 3px;
      left: -30px;
    }
  }

  &--nights {
    &::before {
      position: absolute;
      content: "";
      background-image: url("../img/moon.svg");
      width: 20px;
      height: 20px;
      top: 3px;
      left: -30px;
    }
  }
}

.offers__info {
  display: flex;
  justify-content: space-between;

  // &--elbrus {
  //   padding: 206px 56px 0 22px;
  // }
  //
  // &--belukha {
  //   padding: 206px 38px 0 22px;
  // }
  //
  // &--blacksea {
  //   padding: 206px 45px 0 22px;
  // }
}

.offers__item h3 {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  padding: 22px 0 20px;
}

.offers__info p {
  margin: 0;
}

.offers__additional {
  display: flex;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  justify-content: space-between;
}

.offers__link {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/forward.svg");
    background-repeat: no-repeat;
    top: 3px;
    left: 58.7%;
    height: 14px;
    width: 8px;

    @media (max-width: $tablets-max) {
      left: 68.5%;
    }
  }
}

.offers__link p {
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  margin-top: 60px;
  letter-spacing: 0.6px;
  transform: translateX(-8px);

  @media (max-width: $tablets-max) {
    margin-top: 42px;
    transform: translateX(-12px);
  }
}

.offers__title-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.offers__buttons {
  display: flex;
  transform: translateY(10px);
  margin-right: -4px;
}
