.study {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #d9e7ea;
    bottom: 31px;

    @media (max-width: $tablets-max) {
      display: none;
    }
  }
}

.study__inner {
  padding: 61px 16px 0 13px;
  position: relative;
  z-index: 2;

  @media (max-width: $tablets-max) {
    padding: 98px 112px 0 110px;
  }
}

.study__inner p {
  max-width: 805px;
  line-height: 26px;
  margin-top: 6px;

  @media (max-width: $tablets-max) {
    max-width: 480px;
  }
}

.study__process {
  position: relative;

  h3 {
    margin: 0;
    padding: 22px 0 25px 0;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;

    @media (max-width: $tablets-max) {
      font-size: 18px;
      line-height: 22px;
      padding: 26px 0 25px;
    }
  }
}

.study__line-wrapper {
  position: relative;
}

.study__line {
  @media (max-width: $tablets-max) {
    position: absolute;
    content: "";
    width: 100%;
    height: 125px;
    background: url("../img/line.png");
    background-repeat: no-repeat;
    background-position: center;
    top: 86px;
    transform: translateX(-3px);
  }
}


.study__inner h2 {
  font-weight: 800;
  font-size: 35px;
  line-height: 43px;
  margin-bottom: 26px;

  @media (max-width: $tablets-max) {
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 30px;
  }
}

.study__list {
  @include list;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 10px 0 36px;
  position: relative;


  @media (max-width: $tablets-max) {
    flex-wrap: wrap;
    justify-content: space-between;

    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }
}

.study__item {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 25%;

  &::after {
    position: absolute;
    content: "";
    background-color: #0266c1;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    bottom: -11px;
    left: 3px;
  }

  @media (max-width: $tablets-max) {
    padding: 9px 0;
    width: 43%;

    &::after {
      display: none;
    }
  }

  &--movement {
    // width: 240px;
    margin-right: 21px;

    @media (max-width: $tablets-max) {
      margin-right: 0;
      order: 1;

      &::after {
        bottom: -13px;
        left: 104px;
      }
    }
  }

  &--equipment {
    // width: 260px;
    margin-right: 26px;

    @media (max-width: $tablets-max) {
      margin-right: 0;
      order: 2;

      &::after {
        bottom: -13px;
        left: 107px;
      }
    }
  }

  &--ecology {
    // width: 210px;
    margin-right: 37px;

    @media (max-width: $tablets-max) {
      margin-right: 0;
      padding-top: 123px;
      order: 4;

      &::after {
        top: 99px;
        left: 109px;
      }
    }
  }

  &--obstacles {
    // width: 250px;

    @media (max-width: $tablets-max) {
      margin-right: 0;
      padding-top: 123px;
      order: 3;

      &::after {
        top: 99px;
        left: 105px;
      }
    }
  }

  p {
    font-size: 16px;
    line-height: 23px;
    margin: 0;
    padding: 1px 0 16px 14px;

    @media (max-width: $tablets-max) {
      padding: 6px 0 16px 14px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}
